li {
  list-style-type: none;
}

body{
  margin: 0;
}

.first{
  background-color: #FFFFFF;
  max-width: 1440px;
  padding: 0 140px;

}

.first_line{
  height: 272px;
  background-color: #FFFFFF;
  max-width: 1440px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  padding: 0;
  margin: 100px 0 0 0;

  li{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    ul{
      padding: 0;
      li {
        font-family: Montserrat-Regular, sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: rgba(31, 32, 65, 0.5);

        margin-top: 22px;
        margin-right: 0;
      }
    }

    p{
      font-family: Montserrat-Regular, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 24px;
      color: rgba(31, 32, 65, 0.75);

      margin: 18px 0;
    }

    h3{
      font-family: Montserrat-Regular, sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 12px;
      line-height: 15px;
      text-transform: uppercase;

      margin: 4px 0 0 0;
    }
  }

}
.first_line>li:nth-child(1){
  margin-right: 40px;
}

.first_line>li:nth-child(1),
.first_line>li:nth-child(5){
  width: 260px;

}

.first_line>li:nth-child(2),
.first_line>li:nth-child(3),
.first_line>li:nth-child(4){
  width: 200px;
}






.enter_email {

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  height: 44px;
  width: 260px;
  padding: 0 14px;

  border: 1px solid rgba(31, 32, 65, 0.25);
  box-sizing: border-box;
  border-radius: 4px;

  input {
    font-family: Montserrat-Regular, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    place-content: space-between;
    color: rgba(31, 32, 65, 0.25);

    height: 100%;
    width: 100%;
    padding: 0;

    border: 0
  }
}
.second_line{
  background-color: #FFFFFF;
  max-width: 1440px;
  padding: 0 140px;
  margin: 0;
  border-top: 1px solid rgba(31, 32, 65, 0.25);
  box-shadow: 0 10px 20px rgba(31, 32, 65, 0.05);
}

.container {
  background-color: #FFFFFF;
  max-width: 1440px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  height: 70px;


  p {
    font-family: Montserrat-Regular, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: rgba(31, 32, 65, 0.75);
  }


  div {
    display: flex;
    flex-direction: row;
    align-items: center;

    img {
      height: 24px;
      width: 24px;
      margin-left: 20px;
    }
  }
}

@font-face {
  font-family: "Open-Sans";
  src: url("../font/OpenSans/OpenSans-Regular.ttf") format("ttf");
}

@font-face {
  font-family: "Montserrat-Regular";
  src: url("../font/Montserrat/Montserrat-Regular.woff") format("woff");

}